@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: bold;
  font-weight: 500;
  src: local("Oswald"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.page__body {
  font-family: "Oswald", "Arial", sans-serif;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-header {
  text-transform: uppercase;
}
.page-header__logo-wrapper {
  max-width: 280px;
  box-sizing: border-box;
  min-height: 66px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  padding-top: 10px;
  padding-bottom: 12px;
}
.page-header__logo-name {
  background-image: url("../img/logo-name.svg");
  width: 101px;
  height: 18px;
  margin-top: 14px;
  margin-right: 14px;
}
.page-header__nav-toggle--burger {
  position: relative;
  outline: none;
  border: none;
  background-color: #ffffff;
  width: 21px;
  height: 23px;
  cursor: pointer;
}
.page-header__nav-toggle--burger::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  top: 13px;
  right: 1px;
}
.page-header__nav-toggle--cross {
  outline: none;
  border: none;
  background-color: #ffffff;
  width: 21px;
  height: 23px;
  position: relative;
  cursor: pointer;
  margin-top: 11px;
}
.page-header__nav-toggle--cross::before,
.page-header__nav-toggle--cross::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 1px;
  background-color: #000000;
  top: 11px;
  right: -2px;
}
.page-header__nav-toggle--cross::before {
  transform: rotate(45deg);
}
.page-header__nav-toggle--cross::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .page-header {
    max-width: 708px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }
  .page-header__nav-toggle {
    display: none;
  }
  .page-header__logo-wrapper {
    margin: 0;
    padding: 0;
    margin-top: 24px;
  }
  .page-header__logo-wrapper:hover,
  .page-header__logo-wrapper:focus {
    opacity: 0.8;
  }
  .page-header__logo-wrapper:active {
    opacity: 0.6;
  }
  .page-header__logo-image {
    width: 174px;
    height: auto;
  }
  .page-header__logo-name {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header {
    max-width: 1220px;
    display: flex;
    justify-content: space-between;
  }
  .page-header__nav-toggle {
    display: none;
  }
  .page-header__logo-wrapper {
    margin-top: 53px;
  }
  .page-header__logo-image {
    width: 202px;
    height: auto;
  }
}
.main-nav {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
}
.main-nav--closed {
  display: none;
}
.main-nav--opened {
  display: block;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 320px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
.main-nav__item {
  box-sizing: border-box;
  min-height: 66px;
  padding-top: 22px;
  padding-bottom: 23px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
.main-nav__item a {
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .main-nav {
    display: block;
  }
  .main-nav__list {
    width: 440px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 42px;
  }
  .main-nav__item {
    border: none;
    font-size: 18px;
    line-height: 24px;
    font-weight: normal;
    padding: 0;
  }
  .main-nav__item:hover,
  .main-nav__item:focus {
    opacity: 0.6;
  }
  .main-nav__item:active {
    opacity: 0.3;
  }
  .main-nav__item--current a {
    padding-bottom: 6px;
    border-bottom: 2px solid #68b738;
  }
  .main-nav__item--current:hover,
  .main-nav__item--current:focus {
    opacity: 1;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    margin-top: 70px;
  }
  .main-nav__list {
    margin-top: 0;
    min-width: 480px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .main-nav__list--index a {
    color: #ffffff;
  }
  .main-nav__list--index > .main-nav__item--current a {
    padding-bottom: 6px;
    border-bottom: 2px solid #ffffff;
  }
  .main-nav__item {
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
  }
  .main-nav__item--index:hover,
  .main-nav__item--index:focus {
    opacity: 0.6;
  }
}
